import axios from 'axios'
import { useSnackbar } from 'notistack'
import type { FC } from 'react'
import { useEffect, useState } from 'react'
import { useAuthHeader } from 'react-auth-kit'
import { useParams } from 'react-router'

import {
  Card,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Typography,
  useMediaQuery,
  useTheme
} from '@mui/material'

import type { TempSemesterInfoProps } from 'src/content/manage/Account'
import { EXPENSE_COLOR, INCOME_COLOR } from 'src/content/manage/Account/accountPageColors'
import applyComma from 'src/content/manage/Account/applyComma'
import { COMMON_MESSAGE } from 'src/data/snackbarMessages'
import type { GetAccountInfoResponseData } from 'src/models/apiResponseTypes/accountApiRepsonse'

interface CustomTableCell {
  cellValue: string
  cellColor?: string
}

interface SemsterSummaryProps {
  semesterInfo: TempSemesterInfoProps
  setSemesterInfo: (e: TempSemesterInfoProps) => void
  isRefreshFlag: boolean
}
// TODO::
// eslint-disable-next-line @typescript-eslint/no-unused-vars
const SemesterSummary: FC<SemsterSummaryProps> = ({ semesterInfo, setSemesterInfo, isRefreshFlag }) => {
  const [summary, setSummary] = useState<GetAccountInfoResponseData>()

  const params = useParams()
  const theme = useTheme()
  const isUpperThanSm = useMediaQuery(theme.breakpoints.up('sm'))
  const { enqueueSnackbar } = useSnackbar()
  const authHeader = useAuthHeader()

  const isValidNumbers = !!summary?.totalIncome && !!summary?.totalExpense
  useEffect(() => {
    const fetchData = async () => {
      try {
        const res = await axios({
          method: 'get',
          url: `${process.env.REACT_APP_APIHOST}/club/${params.id}/account/`,
          headers: { Authorization: authHeader() }
        })
        setSummary(res.data)
      } catch {
        enqueueSnackbar(COMMON_MESSAGE.SERVER_ERROR, {
          variant: 'error'
        })
      }
    }
    fetchData()
  }, [isRefreshFlag])

  return (
    <Card style={{ marginBottom: 20 }}>
      <TableContainer>
        <Table>
          <TableBody>
            <TableRow hover>
              <CustomTableCell cellValue={`${semesterInfo.year}년`} />
              <CustomTableCell cellValue={semesterInfo.semester === 'SPRING' ? '1학기' : '2학기'} />
            </TableRow>
          </TableBody>
        </Table>
      </TableContainer>

      <TableContainer>
        <Table>
          {isUpperThanSm && (
            <TableHead>
              <TableRow>
                <TableCell>총 수입</TableCell>
                <TableCell>총 지출</TableCell>
                <TableCell>사용가능금액</TableCell>
              </TableRow>
            </TableHead>
          )}
          <TableBody>
            <TableRow hover>
              <CustomTableCell cellValue={`+${applyComma(summary?.totalIncome)}`} cellColor={INCOME_COLOR} />
              <CustomTableCell cellValue={`-${applyComma(summary?.totalExpense)}`} cellColor={EXPENSE_COLOR} />
              <CustomTableCell
                cellValue={`${isValidNumbers ? applyComma(summary?.totalIncome - summary?.totalExpense) : '0'}`}
              />
            </TableRow>
          </TableBody>
        </Table>
      </TableContainer>
    </Card>
  )
}

export default SemesterSummary

export const CustomTableCell: FC<CustomTableCell> = ({ cellValue, cellColor = 'text.primary' }) => {
  return (
    <TableCell width={'25%'}>
      <Typography variant={'body1'} fontWeight={'bold'} color={cellColor} gutterBottom noWrap>
        {cellValue}
      </Typography>
    </TableCell>
  )
}
