import { useState } from 'react'
import { useAuthUser } from 'react-auth-kit'
import { Helmet } from 'react-helmet-async'
import { useParams } from 'react-router'

import { Container } from '@mui/material'

import PageTitleWrapper from 'src/components/PageTitleWrapper'
import HistoryGrid from 'src/content/manage/Account/HistoryGrid'
import HistoryInput from 'src/content/manage/Account/HistoryInput'
import SemesterSummary from 'src/content/manage/Account/SemesterSummary'
import PageHeader from 'src/content/manage/PageHeader'
import type { PutAccountHistoryRequestData } from 'src/models/apiRequestTypes/accountApiRequest'

export interface TempSemesterInfoProps {
  year: number
  semester: 'SPRING' | 'FALL'
}

const AccountPage = () => {
  const [clickedFee, setClickedFee] = useState<PutAccountHistoryRequestData>()
  const [isActivateRefresh, setIsActivateRefresh] = useState(false)
  // TODO: change this.
  const [semesterInfo, setSemesterInfo] = useState<TempSemesterInfoProps>({
    year: 2023,
    semester: 'SPRING'
  })
  const authUser = useAuthUser()
  const params = useParams()
  const clubId = Number(params.id)
  const club = authUser()?.clubs.filter((c: { id: number }) => c.id === clubId)[0]

  const handleClickedFee: (e: PutAccountHistoryRequestData) => void = e => {
    setClickedFee(e)
  }
  // TODO!
  // const handleChangeSemester = (e: any) => {
  //   setSemesterInfo(e)
  // }
  const refreshInformation = () => {
    setIsActivateRefresh(!isActivateRefresh)
  }
  return (
    <>
      <Helmet>
        <title>회비 관리</title>
      </Helmet>
      <PageTitleWrapper>
        <PageHeader clubName={club.name} buttonText={'회비 설정'} link={`/manage/${clubId}/fee/edit`} />
      </PageTitleWrapper>
      <Container maxWidth={'lg'}>
        <SemesterSummary
          semesterInfo={semesterInfo}
          setSemesterInfo={setSemesterInfo}
          isRefreshFlag={isActivateRefresh}
        />
        <HistoryInput semesterInfo={semesterInfo} clickedFee={clickedFee} refresh={refreshInformation} />
        <HistoryGrid
          semesterInfo={semesterInfo}
          handleRowClick={handleClickedFee}
          isRefreshFlag={isActivateRefresh}
          refresh={refreshInformation}
        />
      </Container>
    </>
  )
}

export default AccountPage
