import axios from 'axios'
import { useSnackbar } from 'notistack'
import type { FC } from 'react'
import { useEffect, useState } from 'react'
import { useAuthHeader } from 'react-auth-kit'
import { useParams } from 'react-router'

import DeleteOutlineIcon from '@mui/icons-material/DeleteOutline'
import {
  Box,
  Button,
  Card,
  CardHeader,
  Chip,
  Divider,
  FormControlLabel,
  Switch,
  Typography,
  useMediaQuery,
  useTheme
} from '@mui/material'
import type {
  GridColDef,
  GridRenderCellParams,
  GridRowParams,
  GridRowSelectionModel,
  GridRowsProp
} from '@mui/x-data-grid'
import {
  DataGrid,
  GRID_CHECKBOX_SELECTION_COL_DEF,
  GridToolbarColumnsButton,
  GridToolbarContainer,
  GridToolbarDensitySelector,
  GridToolbarExport,
  GridToolbarFilterButton,
  GridToolbarQuickFilter
} from '@mui/x-data-grid'

import { DEFAULT_DELETE_MESSAGE } from 'src/data/cofirmDialogueMessages'
import { COMMON_MESSAGE } from 'src/data/snackbarMessages'

import type { AmountType } from 'src/models/account'
import type { GetAccountHistoryResponseData } from 'src/models/apiResponseTypes/accountApiRepsonse'

import ConfirmDialog from 'src/components/ConfirmDialog'

import type { TempSemesterInfoProps } from 'src/content/manage/Account'
import AmountTypeToggle from 'src/content/manage/Account/AmountTypeToggle'
import {
  EXPENSE_COLOR,
  EXPENSE_COLOR_BACKGROUND,
  INCOME_COLOR,
  INCOME_COLOR_BACKGROUND
} from 'src/content/manage/Account/accountPageColors'
import applyComma from 'src/content/manage/Account/applyComma'

interface HistoryGridProps {
  handleRowClick: (e: GetAccountHistoryResponseData) => void
  semesterInfo: TempSemesterInfoProps
  isRefreshFlag: boolean
  refresh: () => void
}

const getTotalHistoryResult = (histories: GetAccountHistoryResponseData[]) =>
  histories.reduce(
    (acc, { amountCategory, amountType, amount }) => {
      if (amountCategory !== '회비') return acc
      if (amountType === 'INCOME') {
        acc.totalFee += amount
        acc.totalMemberCount += 1
        return acc
      }

      acc.totalFee -= amount
      acc.totalMemberCount -= 1
      return acc
    },
    { totalFee: 0, totalMemberCount: 0 }
  )

const HistoryGrid: FC<HistoryGridProps> = ({ handleRowClick, semesterInfo, isRefreshFlag, refresh }) => {
  const [confirmMode, setConfirmMode] = useState(false)
  const [filteredHistories, setFilteredHistories] = useState<GetAccountHistoryResponseData[]>([])
  const [filterAmountType, setFilterAmountType] = useState<AmountType | null>(null)
  const [highlight, setHighlight] = useState(true)
  const [histories, setHistories] = useState<GetAccountHistoryResponseData[]>([])
  const [historySelection, setHistorySelection] = useState<GridRowSelectionModel>([])
  const [paginationModel, setPaginationModel] = useState({
    pageSize: 10,
    page: 0
  })

  const theme = useTheme()
  const params = useParams()

  const isUpperThanSm = useMediaQuery(theme.breakpoints.up('sm'))
  const { enqueueSnackbar } = useSnackbar()
  const authHeader = useAuthHeader()

  const amountChip = (params: GridRenderCellParams<GetAccountHistoryResponseData>) => {
    return (
      <Chip
        label={applyComma(params.row.amount)}
        size={'small'}
        sx={{
          color: params.row.amountType === 'INCOME' ? INCOME_COLOR : EXPENSE_COLOR,
          bgcolor: params.row.amountType === 'INCOME' ? INCOME_COLOR_BACKGROUND : EXPENSE_COLOR_BACKGROUND
        }}
      />
    )
  }
  const columns: GridColDef[] = [
    {
      ...GRID_CHECKBOX_SELECTION_COL_DEF,
      headerName: '선택'
    },
    { field: 'amountCategory', headerName: '분류', flex: 0.2, sortable: true, filterable: true },
    { field: 'detail', headerName: '내용', flex: 0.3 },
    {
      field: 'amount',
      headerName: '금액',
      flex: 0.2,
      sortable: true,
      filterable: true,
      renderCell: amountChip
    },
    {
      field: 'usedAt',
      headerName: '일자',
      flex: 0.2,
      sortable: true,
      filterable: true,
      renderCell: (params: GridRenderCellParams<GetAccountHistoryResponseData>) => {
        return simplifyDate(params.row.usedAt)
      }
    }
  ]

  const columnsForXS: GridColDef[] = [
    {
      ...GRID_CHECKBOX_SELECTION_COL_DEF,
      headerName: '선택'
    },
    {
      field: 'amountCategory',
      headerName: '분류',
      flex: 0.2,
      sortable: false,
      filterable: false
    },
    {
      field: 'amount',
      headerName: '금액',
      flex: 0.3,
      sortable: false,
      filterable: false,
      renderCell: amountChip
    },
    {
      field: 'usedAt',
      headerName: '일자',
      flex: 0.2,
      sortable: false,
      filterable: false,
      renderCell: (params: GridRenderCellParams<GetAccountHistoryResponseData>) => {
        return simplifyDate(params.row.usedAt)
      }
    }
  ]

  const { totalFee, totalMemberCount } = getTotalHistoryResult(histories)

  useEffect(() => {
    const fetchData = async () => {
      try {
        const res = await axios({
          method: 'get',
          url: `${process.env.REACT_APP_APIHOST}/club/${params.id}/account/history?year=${semesterInfo.year}&semester=${semesterInfo.semester}`,
          headers: { Authorization: authHeader() }
        })
        setHistories(res.data)
        setFilteredHistories(res.data)
      } catch {
        enqueueSnackbar(COMMON_MESSAGE.SERVER_ERROR, {
          variant: 'error'
        })
      }
    }
    fetchData()
  }, [isRefreshFlag])

  useEffect(() => {
    if (filterAmountType) {
      const filtered: GetAccountHistoryResponseData[] = histories.filter(obj => obj.amountType === filterAmountType)
      setFilteredHistories(filtered)
      return
    }
    setFilteredHistories(histories)
  }, [filterAmountType, isRefreshFlag])

  const handleHistoryDelete: React.MouseEventHandler<HTMLButtonElement> = async () => {
    try {
      historySelection.map(async accountDetailId => {
        await axios({
          method: 'delete',
          url: `${process.env.REACT_APP_APIHOST}/club/${params.id}/account/history/${accountDetailId}`,
          headers: { Authorization: authHeader() }
        })
      })
      enqueueSnackbar(COMMON_MESSAGE.DELETE_SUCCESS, { variant: 'success' })
      setConfirmMode(false)
      refresh()
    } catch (error) {
      enqueueSnackbar(COMMON_MESSAGE.DELETE_FAIL, { variant: 'error' })
      setConfirmMode(false)
    }
  }

  const handleColorEnable: () => void = () => {
    setHighlight(!highlight)
  }

  const Toolbar = () => {
    if (historySelection.length >= 1) {
      return (
        <GridToolbarContainer>
          <Button
            variant={'text'}
            size={'small'}
            startIcon={<DeleteOutlineIcon fontSize={'small'} />}
            onClick={() => {
              setConfirmMode(true)
            }}
          >
            {historySelection.length}개 내역 삭제
          </Button>
        </GridToolbarContainer>
      )
    }
    return (
      <GridToolbarContainer>
        <GridToolbarColumnsButton />
        <GridToolbarFilterButton sx={{ '& svg': { width: '18px', height: '18px' } }} />
        <GridToolbarDensitySelector />
        <GridToolbarExport />
        <GridToolbarQuickFilter />
      </GridToolbarContainer>
    )
  }

  const simplifyDate = (usedAt: Date) => {
    const date = new Date(usedAt)
    return `${date.getMonth() + 1} / ${date.getDate()}`
  }

  return (
    <Card style={{ marginBottom: 20 }}>
      <CardHeader
        title={
          <Box display={'flex'} flexDirection={'row'} alignItems={'center'} justifyContent={'space-between'}>
            계좌내역
            <Box display={'flex'} flexDirection={'row'}>
              <FormControlLabel
                value={'COLOR'}
                control={<Switch onChange={handleColorEnable} color={'primary'} defaultChecked />}
                label={'COLOR'}
                labelPlacement={isUpperThanSm ? 'start' : 'top'}
              />
              <AmountTypeToggle
                filteredFeeType={filterAmountType}
                setFilteredFeeType={(event, value) => {
                  setFilterAmountType(value)
                }}
              />
            </Box>
          </Box>
        }
      />
      <Box ml={2} mb={1} display={'flex'} flexDirection={'row'} alignItems={'baseline'}>
        <Typography variant={'h5'}>{totalMemberCount}</Typography>
        <Typography variant={'body2'}>명의 회원이 총&nbsp;</Typography>
        <Typography variant={'h5'}>{applyComma(totalFee)}</Typography>
        <Typography variant={'body2'}>원을 입금했어요</Typography>
      </Box>
      <Card>
        <Divider />
        <Box
          sx={{
            '& .font--bold': {
              fontWeight: 'bold'
            },
            '& .amount--INCOME': {
              backgroundColor: INCOME_COLOR_BACKGROUND
            },
            '& .amount--EXPENSE': {
              backgroundColor: EXPENSE_COLOR_BACKGROUND
            }
          }}
        >
          <DataGrid
            autoHeight
            rows={filteredHistories as GridRowsProp}
            columns={isUpperThanSm ? columns : columnsForXS}
            paginationModel={paginationModel}
            onPaginationModelChange={setPaginationModel}
            pageSizeOptions={[5, 10, 20]}
            slots={{ toolbar: Toolbar }}
            pagination
            getCellClassName={params => {
              const style = params.field === 'amountCategory' || params.field === 'amount' ? 'font--bold' : ''
              return style
            }}
            rowSelectionModel={historySelection}
            onRowSelectionModelChange={setHistorySelection}
            onCellClick={params => !isUpperThanSm && enqueueSnackbar(params.row.detail, { variant: 'info' })}
            getRowClassName={params => {
              const style = highlight ? `amount--${params.row.amountType}` : ''
              return style
            }}
            onRowClick={(e: GridRowParams<GetAccountHistoryResponseData>) => {
              handleRowClick(e.row)
            }}
            checkboxSelection
            disableRowSelectionOnClick
          />
        </Box>
      </Card>
      <ConfirmDialog
        open={confirmMode}
        handleClose={() => setConfirmMode(false)}
        handleSubmit={handleHistoryDelete}
        confirmMessage={DEFAULT_DELETE_MESSAGE}
      />
    </Card>
  )
}

export default HistoryGrid
