import type { FC } from 'react'

import { Box, ToggleButton, ToggleButtonGroup } from '@mui/material'

import type { AmountType } from 'src/models/account'

interface FeeTypeToggleProps {
  filteredFeeType: AmountType | null
  setFilteredFeeType: (event: any, newFilter: AmountType | null) => void
}

const AmountTypeToggle: FC<FeeTypeToggleProps> = ({ filteredFeeType, setFilteredFeeType }) => {
  return (
    <Box ml={4}>
      <ToggleButtonGroup color="primary" value={filteredFeeType} exclusive onChange={setFilteredFeeType}>
        <ToggleButton value="INCOME">수입</ToggleButton>
        <ToggleButton value="EXPENSE">지출</ToggleButton>
      </ToggleButtonGroup>
    </Box>
  )
}

export default AmountTypeToggle
